.card {
  display: grid;
  grid-template-columns: 100px;
  grid-template-rows: 150px 150px 80px;
  grid-template-areas: "image" "text" "links";
  /* background: white; */
  box-shadow: 3px 3px 7px rgba(0, 0, 0, 0.9);
  font-family: roboto;
  font-size: 20px;
  text-align: center;
  max-width: 450px;
}
.card-image .image {
  grid-area: image;
  max-width: 300px;
  /* background: url(""); */
  background-size: contain;
}
img {
  border-radius: 15px;
  border-bottom-left-radius: 10px;
  margin-bottom: 2rem;
  margin-top: 2rem;
}

li.list-group-item {
  border: none;
}
.card-text .text {
  grid-area: text;
  margin: 1rem;
  font-size: 2rem;
}
.card-links {
  grid-area: links;
}
.card-text .date {
  color: rgb(255, 7, 110);
  font-size: 13px;
}
.card-text p {
  color: #000000;
  font-size: 1.6rem;
  font-weight: 300;
  text-align: left;
  margin: 1rem;
}

.card-text h2 {
  margin-top: 0px;
  font-family: Barriecito, "cursive";
  font-size: 3rem;
  padding-top: 1rem;
  color: #00b3cd;
}
.card-text h3 {
  margin-top: 0px;
  font-family: Barriecito, "cursive";
  font-size: 2.25rem;
  color: #fea905;
  text-align: left;
  margin-left: 1rem;
}
.card-text h4 {
  margin-top: 0px;
  font-size: 28px;
}
.card-links {
  grid-area: links;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  background: #00b3cd;
}
.card-links .link {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  color: white;
  padding: 0.5rem;
  font-family: roboto;
  font-weight: bold;
  font-size: 1.5rem;
}
.card:hover {
  -webkit-transform: scale(1.15);
          transform: scale(1.15);
  box-shadow: 5px 5px 15px rgba(0, 0, 0, 0.6);
}
.card {
  transition: 0.5s ease;
  cursor: pointer;
  margin: 1rem;
}
.button {
  font-family: Barriecito, "cursive";
  font-size: 1rem;
  color: #ffffff;
  background-color: #7677b3;
  border: none;
  border-radius: 10px;
  padding: 0.75rem;
}

:root {
  --brandColor: hsl(208, 85%, 46%);
    --background: rgb(247, 247, 247);
    --textDark: hsla(0, 0%, 0%, 0.66);
    --textLight: hsla(0, 0%, 0%, 0.33);
  }
  
  body  {
    background: linear-gradient(to right top, #3299e8, #ebc36c);
    height: 100vh;
    color: hsla(0, 0%, 0%, 0.66);
    color: var(--textDark);
  }
  
  .field:not(:last-child) {
    margin-bottom: 2rem;
  }
  
  .register {
    margin-top: 5rem;
    background: linear-gradient(
      to right bottom,
      rgba(255, 255, 255, 0.7),
      rgba(255, 255, 255, 0.3)
    );
    border-radius: 10px;
  }
  
  .left,
  .right {
    padding: 4.5rem;
  }
  
  .left {
    border-right: 2px solid rgb(247, 247, 247);
    border-right: 2px solid var(--background);
  }
  
  .left .title {
    font-weight: 800;
    letter-spacing: -2px;
  }
  
  .left .colored {
    color: hsl(208, 85%, 46%);
    color: var(--brandColor);
    font-weight: 500;
    margin-top: 1rem !important;
    letter-spacing: -1px;
  }
  
  .left p {
    color: hsla(0, 0%, 0%, 0.33);
    color: var(--textLight);
    font-size: 1.15rem;
  }
  
  .right .title {
    font-weight: 800;
    letter-spacing: -1px;
  }
  
  .right .description {
    margin-top: 1rem;
    margin-bottom: 1rem !important;
    color: hsla(0, 0%, 0%, 0.33);
    color: var(--textLight);
    font-size: 1.15rem;
  }
  
  .right small {
    color: hsla(0, 0%, 0%, 0.33);
    color: var(--textLight);
  }
  
  input {
    font-size: 1rem;
  }
  
  input:focus {
    border-color: hsl(208, 85%, 46%) !important;
    border-color: var(--brandColor) !important;
    box-shadow: 0 0 0 1px hsl(208, 85%, 46%) !important;
    box-shadow: 0 0 0 1px var(--brandColor) !important;
  }
  
  .fab,
  .fas {
    color: hsla(0, 0%, 0%, 0.33);
    color: var(--textLight);
    margin-right: 1rem;
  }
